<template>
  <div style="width: 100%; height: 55em">
    <div
      style="width: 98%; height: 100%; background-color: white; margin-top: 2%"
    >
      <div>
        <div style="width: 96%; height: 5em; margin-top: 2%">
          <div style="float: left; margin-left: 2%">
            <el-select
              v-model="value"
              placeholder="请选择"
              style="
                position: relative;
                top: 1.2em;
                left: -15%;
                border: 1px solid rgb(233, 233, 233);
                width: 30%;
              "
            >
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
            <el-input
              placeholder="请输入内容"
              suffix-icon="el-icon-search"
              v-model="search"
              style="
                position: relative;
                top: 1.2em;

                border: 1px solid rgb(233, 233, 233);
                width: 50%;
              "
            ></el-input>
          </div>

          <el-button
            type="primary"
            @click="openAddDialog()"
            style="position: relative; top: 1.2em; right: -18em"
            >添加系统角色</el-button
          >

          <!-- 添加弹窗 -->
          <el-dialog :visible.sync="dialogVisible" title="添加企业" width="40%">
            <el-form
              ref="addForm"
              :model="newBusiness"
              label-width="100px"
              :rules="rules"
            >
              <el-form-item label="企业名称" prop="rolename">
                <el-input
                  v-model="newBusiness.rolename"
                  style="width: 100%; border: 1px solid rgb(233, 233, 233)"
                ></el-input>
              </el-form-item>
              <el-form-item label="描述" prop="describe">
                <el-input
                  v-model="newBusiness.describe"
                  style="width: 100%; border: 1px solid rgb(233, 233, 233)"
                ></el-input>
              </el-form-item>
              <el-form-item label="权限">
                <el-tree
                  :data="data"
                  show-checkbox
                  :default-expanded-keys="[0]"
                  node-key="id"
                  ref="tree"
                  highlight-current
                  :props="defaultProps"
                  style="
                    width: 100%;
                    height: 8em;
                    border: 1px solid rgb(233, 233, 233);
                    overflow-y: scroll;
                  "
                >
                </el-tree>
              </el-form-item>
              <el-form-item label="状态" prop="status">
                <el-radio-group v-model="newBusiness.status">
                  <el-radio :label="1">启用</el-radio>
                  <el-radio :label="0">禁用</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false">取消</el-button>
              <el-button type="primary" @click="addRoles">确认</el-button>
            </div>
          </el-dialog>

          <!-- 编辑弹窗 -->
          <el-dialog
            :visible.sync="editDialogVisible"
            title="编辑企业"
            width="40%"
          >
            <el-form
              ref="editForm"
              :model="editBusiness"
              label-width="100px"
              :rules="rules"
            >
              <el-form-item label="企业名称" prop="rolename">
                <el-input
                  v-model="editBusiness.rolename"
                  style="width: 100%; border: 1px solid rgb(233, 233, 233)"
                ></el-input>
              </el-form-item>
              <el-form-item label="描述" prop="describe">
                <el-input
                  v-model="editBusiness.describe"
                  style="width: 100%; border: 1px solid rgb(233, 233, 233)"
                ></el-input>
              </el-form-item>
              <el-form-item label="权限">
                <el-tree
                  :data="data"
                  show-checkbox
                  node-key="id"
                  :default-expanded-keys="[0]"
                  ref="tree"
                  highlight-current
                  :props="defaultProps"
                  style="
                    width: 100%;
                    height: 8em;
                    border: 1px solid rgb(233, 233, 233);
                    overflow-y: scroll;
                  "
                >
                </el-tree>
              </el-form-item>
              <el-form-item label="状态" prop="status">
                <el-radio-group v-model="editBusiness.status">
                  <el-radio :label="1">启用</el-radio>
                  <el-radio :label="0">禁用</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="editDialogVisible = false">取消</el-button>
              <el-button type="primary" @click="saveEdit">保存</el-button>
            </div>
          </el-dialog>
        </div>
      </div>

      <div style="width: 96%; height: 90%; background-color: white">
        <el-table
          :data="tableData"
          style="width: 100%"
          stripe
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column prop="rolename" label="角色名称"></el-table-column>
          <el-table-column prop="describe" label="角色描述"></el-table-column>
          <el-table-column prop="powers" label="角色权限配置">
            <template slot-scope="scope">
              <span
                style="
                  overflow: hidden;
                  white-space: nowrap;
                  text-overflow: ellipsis;
                  font-size: 14px;
                "
                >{{ getNameByIdList(scope.row.powers) }}</span
              >
            </template>
          </el-table-column>

          <el-table-column prop="status" label="角色启用状态">
            <template slot-scope="scope">
              {{ scope.row.status === 1 ? "启用" : "禁用" }}
            </template>
          </el-table-column>
          <el-table-column prop="lasttime" label="最后编辑时间">
            <template slot-scope="scope">
              {{ formatDate(scope.row.lasttime) }}
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button
                @click="openEditDialog(scope.row)"
                type="text"
                icon="el-icon-edit"
              ></el-button>
              <el-button
                @click="delOpen(1, scope.row)"
                type="text"
                icon="el-icon-delete"
              ></el-button>
              <el-button
                @click="stopRoles(scope.row)"
                type="text"
                icon="el-icon-switch-button"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div
          style="
            width: 100%;
            height: 10%;
            display: flex;
            align-items: center;
            justify-content: center;
          "
        >
          <div
            style="
              display: flex;
              margin-left: 3%;
              margin-right: 3%;
              width: 38%;
              align-items: center;
              justify-content: center;
            "
          >
            <p style="width: 35%; margin-left: 1%; margin-right: 3%">
              已选中的行数：{{ selectedCount }}
            </p>
            <el-button style="margin-left: 1%" type="danger" @click="delOpen(2)"
              >批量删除</el-button
            >
            <el-button
              style="margin-left: 1%"
              type="danger"
              @click="stopAllRoles()"
              >批量启用/停用</el-button
            >
          </div>
          <div class="block">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-sizes="[10, 20, 30, 40, 50]"
              :page-size="100"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total"
            >
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getRoleslist,
  addRoles,
  upRoles,
  delRoles,
  getSearchroles,
  stopRoles,
  getMenu,
} from "../../api/login_http";

export default {
  data() {
    return {
      //下拉框
      options: [
        { value: "选项1", label: "cr-01" },
        { value: "选项2", label: "cr-02" },
      ],
      value: "",
      //列表
      tableData: [],
      //分页
      obj: { page: 1, pagelimit: 10 },
      total: 0,
      //选中
      selectedCount: 0,
      selection: [],
      //添加
      dialogVisible: false,
      newBusiness: {
        rolename: "",
        describe: "",
        powers: [],
        status: null,
      },
      rules: {
        rolename: [
          { required: true, message: "请输入企业名称", trigger: "blur" },
        ],
        describe: [{ required: true, message: "请输入描述", trigger: "blur" }],
        // You can add more rules as needed for other fields
      },
      //编辑
      editDialogVisible: false,
      editBusiness: {
        rolename: "",
        describe: "",
        powers: [],
        status: null,
      },
      //搜索
      search: "",
      //树形数据
      data: [
        {
          id: 0,
          label: "开放所有权限",
          children: [
            { id: 1, label: "首页" },
            { id: 2, label: "企业管理" },
            {
              id: 3,
              label: "通话统计",
              children: [{ id: 4, label: "并发统计" }],
            },
            {
              id: 5,
              label: "权限管理",
              children: [
                { id: 6, label: "系统角色管理" },
                { id: 7, label: "系统用户管理" },
              ],
            },
          ],
        },
      ],
      defaultProps: {
        children: "children",
        label: "label",
      },
      //权限列表
      arr_data: [],
    };
  },
  mounted() {
    //默认选中下拉框
    this.value = this.options[0].value;
    //获取角色列表
    this.getRoleslist();
    //获取权限列表
    this.getMenu();
  },
  watch: {
    //监听搜索框
    search() {
      this.getSearchroles();
    },
  },
  methods: {
    //获取权限id
    getCheckedKeys() {
      return this.$refs.tree.getCheckedKeys();
    },
    //选中权限
    setCheckedKeys() {
      this.$refs.tree.setCheckedKeys(this.editBusiness.powers);
    },
    //获取权限
    getMenu() {
      getMenu().then((res) => {
        this.arr_data = res.data.data;
      });
    },
    //对应权限
    getNameByIdList(idList) {
      const idListArray = idList.split(",").map(Number);
      const names = idListArray.map((id) => {
        const item = this.arr_data.find((item) => item.id === id);
        return item ? item.name : null;
      });
      return names.filter((name) => name !== null).join(", ");
    },
    //每页有几条数据
    handleSizeChange(val) {
      this.obj.pagelimit = val;
      this.getRoleslist();
    },
    //当前在第几页
    handleCurrentChange(val) {
      this.obj.page = val;
      this.getRoleslist();
    },
    //获取数据列表
    getRoleslist() {
      getRoleslist(this.obj).then((res) => {
        this.tableData = res.data.data;
        this.total = res.data.pagecount;
      });
    },
    //计算被选中的列表数量
    handleSelectionChange(selection) {
      this.selectedCount = selection.length;
      this.selection = selection;
    },
    //打开或关闭弹窗
    openAddDialog() {
      this.dialogVisible = !this.dialogVisible;
    },
    //添加数据
    addRoles() {
      this.newBusiness.powers = this.$refs.tree.getCheckedKeys().join(",");
      addRoles(this.newBusiness).then((res) => {
        if (res.msg == "系统角色添加成功") {
          this.$message({
            type: "success",
            message: "添加成功!",
          });
          this.openAddDialog();
        } else {
          this.$message({
            message: res.msg,
            type: "warning",
          });
        }
        this.getRoleslist();
      });
    },
    // 打开编辑弹窗，并回显被选中行的数据
    openEditDialog(index) {
      // 将被选中行的数据赋值给编辑数据对象
      this.editBusiness = { ...index };
      this.editBusiness.powers = index.powers.split(",").map(Number);
      // 显示编辑弹窗
      this.editDialogVisible = true;
      this.$nextTick(() => {
        if (this.$refs.tree) {
          this.setCheckedKeys();
        }
      });
    },
    // 保存编辑后的数据
    saveEdit() {
      // 编辑后的数据保存操作
      this.editBusiness.powers = this.$refs.tree.getCheckedKeys().join(",");
      upRoles(this.editBusiness).then((res) => {
        if (res.msg == "修改成功") {
          this.$message({
            type: "success",
            message: "修改成功!",
          });
          this.editDialogVisible = false;
        } else {
          this.$message({
            message: res.msg,
            type: "warning",
          });
        }
        // 重新获取角色列表数据
        this.getRoleslist();
      });
    },
    // 删除角色
    delOpen(del, index) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          if (del == 1) {
            this.delRoles(index);
          } else if (del == 2) {
            this.delAllRoles();
          }
          this.$message({
            type: "success",
            message: "删除成功!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    // 删除角色
    delRoles(index) {
      const obj = { id: index.id };
      delRoles(obj).then(() => {
        this.getRoleslist();
      });
    },
    //删除选中角色
    delAllRoles() {
      for (var i = 0; i < this.selection.length; i++) {
        this.delRoles(this.selection[i]);
      }
    },
    //搜索
    getSearchroles() {
      const obj = { rolename: this.search };
      getSearchroles(obj).then((res) => {
        console.log(res);
        this.tableData = res.data.data;
      });
    },
    //改变数据状态
    stopRoles(index) {
      const obj = { id: index.id, status: !index.status };
      stopRoles(obj).then(() => {
        this.getRoleslist();
      });
    },
    //改变选中数据状态
    stopAllRoles() {
      for (var i = 0; i < this.selection.length; i++) {
        this.stopRoles(this.selection[i]);
      }
    },
    //修改时间格式
    formatDate(timestamp) {
      // 如果时间戳是以秒为单位的，将其乘以 1000 转换为毫秒
      const milliseconds = timestamp * 1000;
      const date = new Date(milliseconds);
      const year = date.getFullYear();
      const month = String(date.getMonth() + 1).padStart(2, "0"); // Month starts from 0
      const day = String(date.getDate()).padStart(2, "0");
      return `${year}-${month}-${day}`;
    },
  },
};
</script>

<style>
body {
  overflow: hidden;
}

.cell {
  text-align: center;
}

.el-message {
  min-width: 40px !important;
}
</style>